.header {
  padding-top: 38px;
  padding-bottom: 29px;
  background: $gradient;
  color: $color-default-white;
  font-family: $ff-alumni;

  @include vp-1199 {
    padding-top: 35px;
    padding-bottom: 70px;
    background: $gradient-tablet;
  }

  @include vp-767 {
    padding-top: 15px;
    padding-bottom: 58px;
    background: $gradient-mobile;
  }
}

.header__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  position: relative;
  z-index: 1;
  justify-content: space-between;

  @include vp-1199 {
    align-items: baseline;
  }

  @include vp-767 {
    align-items: flex-start;
  }

  svg {
    fill: $color-default-white;

    @include vp-1199 {
      width: 82px;
      height: 17px;
    }

    @include vp-767 {
      width: 69px;
      height: 24px;
    }
  }
}

.header__tel-wrapper {
  max-width: 80%;
}

.header__tel {
  font-weight: 600;
  font-size: 26px;
  line-height: 15px;
  letter-spacing: 0.03em;
  color: $color-default-white;
  margin-left: 10px;

  @include vp-1199 {
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 24px;
    font-weight: 600;
  }
}

.header__block {
  display: flex;
  margin: -3.5% 0 0 19px;

  @include vp-1199 {
    margin: 0;
  }

  @include vp-767 {
    display: block;
    margin-top: 61px;
  }
}

.header__text {
  margin-top: 130px;
  margin-right: 65px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 50%;

  @include vp-1199 {
    margin-top: 65px;
    margin-right: 30px;
  }

  @include vp-767 {
    margin: 0;
    max-width: 100%;
  }

  span {
    font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: 0.03em;
    margin-right: 49px;

    @include vp-1199 {
      font-size: 34px;
      line-height: 41px;
      margin-right: 3px;
    }

    @include vp-767 {
      align-self: flex-end;
      font-size: 28px;
      line-height: 34px;
    }
  }
}

.header__title {
  margin: 15px 0 50px;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto 1fr;
  gap: 16px;
  align-items: self-start;

  @include vp-1199 {
    margin: 9px 0 36px;
  }

  @include vp-767 {
    margin: 15px 0 22px;
    gap: 0;
    width: 100%;
    align-items: flex-start;
    justify-content: space-between;
  }

  h1 {
    padding: 0;
    margin: 0 0 34px;
    font-weight: 600;
    font-size: 92px;
    line-height: 110px;
    text-align: right;
    letter-spacing: 0.05em;
    -webkit-text-stroke: 4px $color-default-white;
    text-transform: uppercase;
    max-width: 248px;


    @include vp-1199 {
      font-size: 72px;
      line-height: 86px;
      letter-spacing: 0.05em;
      text-transform: none;
      margin: 0 0 21px;
    }

    @include vp-767 {
      margin: 0 0 22px;
      max-width: none;
      min-height: 140px;
    }
  }
}

.header__compound {
  list-style-type: none;
  padding: 0;
  margin: 122px 0 0;
  text-align: end;
  font-family: $ff-roboto;
  font-size: 14px;
  line-height: 21px;
  grid-row: 1 / 3;

  @include vp-1199 {
    font-size: 14px;
    line-height: 150%;
    margin: 88px 9px 0 0;
  }

  @include vp-767 {
    margin: 76px -8px 20px 0;
  }
}

.header__img {
  align-self: center;
  width: 44%;

  @include vp-1199 {
    margin-top: 17px;
    width: 100%;
    align-self: auto;
  }

  @include vp-767 {
    display: none;
  }

  img {
    @include vp-1199 {
      min-height: 390px;
    }
  }
}

.header__button {
  padding: 19px;
  min-width: 234px;
  margin: 0 0 15px 15px;

  @include vp-1199 {
    margin: 0;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    min-width: 187px;
  }

  @include vp-767 {
    grid-column: 1/3;
    min-width: 100%;
    //min-width: calc(100% + 106px);
    //margin-left: -106px;
  }
}
